/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ Start of Font ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

@font-face {
  font-family: Shabnam;
  src: url("/Content/fonts/Shabnam.ttf"),
    url("../fonts/Shabnam-FD.woff2") format("woff2"),
    url("../fonts/Shabnam-FD.woff") format("woff"),
    url("../fonts/Shabnam-FD.ttf") format("truetype");
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: "IRANSans-web";
  src: url("../fonts/IRANSans-web.ttf"),
    url("../fonts/IRANSans-web.otf") format("truetype");
  font-display: swap;
  font-weight: normal;
}

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ End of Font ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ Start of General Styles ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-weight: normal;
  direction: rtl;
}

:root {
  --blue-light-color: #a8c6de;
  --purple-primary-color: #231e39;
  --purple-secondary-color: #28223f;
  --purple-tertiary-color: #1f1a32;
  --dark-primary-color: #333300;
  --yellow-primary-color: #ffdf00;
  --nav-load-time: 400ms;
  --header-load-time: 400ms;
  --names-header-time: 3s;
  --logo-header-time: 300ms;
  --nav-link-load-time: 1.5s;
  --MyPic-delay-time: calc(
    (var(--nav-load-time) + var(--nav-link-load-time)) + 100ms
  );
}

body,
html {
  overflow-x: hidden;
  width: 100vw;
}

body {
  font-family: Shabnam, "B Morvarid", "IRANSans-web", "B Koodak", Georgia;
  overflow-x: hidden;
  background-image: linear-gradient(to right, #fff, #ffe6ff 80%);
  overflow-anchor: none;
}

html {
  scroll-behavior: smooth;
}

a:focus,
a:hover {
  color: var(--dark-primary-color);
}

a {
  text-decoration: none;
}

.fade-in {
  opacity: 0;
  transition: opacity 250ms ease-in;
}

.fade-in.appear {
  opacity: 1;
}

.main-titles {
  text-align: center;
  font-weight: bold;
  transform: translateY(-50px);
  opacity: 0;
  transition: opacity 300ms ease-in, transform 300ms ease-in;
  color: var(--dark-primary-color);
}

.main-titles.appear {
  transform: translateY(0);
  opacity: 1;
}

#GoToTopButton {
  position: fixed;
  display: none;
  right: 30px;
  bottom: 20px;
  color: var(--blue-light-color);
  background: var(--purple-primary-color);
  z-index: 10099;
  font-size: 18px;
  border: none;
  outline: none;
  border-radius: 4px;
  padding: 12px 20px;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

/*padding top for anchor*/
.padding-top-2 {
  padding-top: 2rem;
}

.padding-top-3 {
  padding-top: 3rem;
}

.padding-top-5 {
  padding-top: 5rem;
}

/*GoToTopButton*/
#GoToTopButton:hover {
  background: var(--blue-light-color);
  color: var(--purple-primary-color);
}

/* Width */
::-webkit-scrollbar {
  width: 0.65vw;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: rgba(168, 198, 222, 0.6);
  border-radius: 10px;
  box-shadow: inset 4px 4px 7px var(--blue-light-color);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color: #d4af37;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: var(--yellow-primary-color);
}

/* Handle when focused */
::-webkit-scrollbar-thumb:focus {
  background-color: var(--yellow-primary-color);
}

@media (min-width: 700px) {
  body {
    overflow-y: hidden;
  }
}

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ End of General Styles ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ Start of Loading ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

.loader-container {
  background: rgba(0, 0, 0, 0.5);
  z-index: 99999;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  opacity: 1;
  transition: opacity 0.5s ease;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  visibility: visible;
}

.loader-container.hidden {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease, visibility 0s 0.5s;
}

.loader {
  width: 100px;
  aspect-ratio: 1;
  padding: 1rem;
  box-sizing: border-box;
  display: grid;
  background: #fff;
  filter: blur(5px) contrast(10) hue-rotate(180deg);
  mix-blend-mode: darken;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 1rem;
}

.loader:before,
.loader:after {
  content: "";
  grid-area: 1/1;
  margin: 2.9rem 0;
  border-radius: 100px;
  background: green;
  animation: l4 2s infinite linear;
}

.loader:after {
  --s: -1;
}

@keyframes l4 {
  100% {
    transform: rotate(calc(var(--s, 1) * 1turn));
  }
}

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ End of Loading ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ Start of Welcoming ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

#welcome-overlay {
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 0 2rem;
  width: 100%;
  height: 50%;
  background: rgba(173, 173, 173, 0.8);
  color: #ffffff;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 2s ease, transform 2s ease;
  transform: translateY(100%);
  z-index: 10000;
}

#welcome-overlay h3 {
  font-weight: 700;
  font-size: 5rem;
}

#welcome-overlay p {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  direction: ltr;
}

#welcome-overlay.showWelcome {
  opacity: 1;
  transform: translateY(0);
}

#welcome-overlay.hideWelcome {
  animation: shakeAndSlide 2s ease forwards;
}

button#got-it-button {
  padding: 1.5rem 4rem;
  font-size: 2.5rem;
  background: linear-gradient(135deg, #3a3252, #7a3a9d);
  color: var(--blue-light-color);
  border: none;
  cursor: pointer;
  border-radius: 10px 10px 40px 40px;
}

button#got-it-button:hover {
  background-color: #ccc;
}

@keyframes shakeAndSlide {
  0% {
    transform: translateY(0) translateX(0);
    opacity: 1;
  }

  20% {
    transform: translateY(10px) translateX(-10px);
  }

  40% {
    transform: translateY(20px) translateX(10px);
  }

  60% {
    transform: translateY(30px) translateX(-10px);
  }

  80% {
    transform: translateY(40px) translateX(10px);
  }

  100% {
    transform: translateY(100%) translateX(0);
    opacity: 0;
  }
}

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ End of Welcoming ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ Start of Header ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

.header {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  direction: ltr;
  font-size: 4.2rem;
  height: 210px;
  background: var(--purple-secondary-color);
  animation: header-load var(--header-load-time) ease-in;
}

.header-p {
  animation: names-header var(--names-header-time) ease-out;
}

.header p:nth-child(1) {
  color: var(--blue-light-color);
  letter-spacing: 1px;
}

.header p:nth-child(2) {
  color: var(--blue-light-color);
  letter-spacing: 1px;
}

#logo-header {
  animation: var(--logo-header-time) ease-out 900ms 1 logo-header;
  animation-fill-mode: backwards;
}

#logo-header img {
  width: 100%;
  max-width: 192px;
  height: auto;
  object-fit: contain;
}

#header-nav {
  color: var(--dark-primary-color);
  justify-content: space-between;
  height: 70px;
  background-image: linear-gradient(to right, #ffde5d, #ffd633, #ffd11a);
  width: 100%;
  float: right;
  margin: 0 auto;
  transition: 0.4s ease-out;
  animation: nav-load var(--nav-load-time) ease-in;
  align-items: center;
  z-index: 10000;
  box-shadow: 0 8px 15px 2px var(--yellow-primary-color);
}

@media screen and (max-width: 500px) {
  #header-nav {
    box-shadow: 0 6px 12px var(--yellow-primary-color);
  }
}

@media screen and (max-width: 350px) {
  #header-nav {
    box-shadow: 0 4px 10px var(--yellow-primary-color);
  }

  #entrepreneur-quotes-container .cards {
    width: 230px !important;
    height: 350px !important;
  }
}

nav .openMenu .fa-bars {
  font-size: 3rem;
}

nav .mainMenu {
  list-style: none;
  display: flex;
}

nav .openMenu {
  font-size: 2rem;
  padding: 20px;
  display: none;
  cursor: pointer;
  width: 20%;
  margin: 0 auto;
}

#header-nav ul li {
  position: relative;
  margin: 0 auto;
  transition: 0.4s ease-out;
  animation-name: nav-link-load;
  animation-duration: var(--nav-link-load-time);
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
  transform: scale(0);
  opacity: 0.6;
}

#header-nav ul li.active {
  opacity: 1;
  text-shadow: -3px 3px 7px #4d4d00;
}

#header-nav ul li:hover {
  opacity: 1;
}

#header-nav ul li:nth-child(odd) {
  animation-name: nav-odd-link-load;
  animation-timing-function: ease-out;
  animation-delay: var(--nav-load-time);
  animation-fill-mode: forwards;
  transform: translateY(10000%);
}

#header-nav ul li:nth-child(even) {
  animation-name: nav-even-link-load;
  animation-timing-function: ease-in;
  animation-delay: var(--nav-load-time);
  animation-fill-mode: forwards;
  transform: translateY(-1000%);
}

#header-nav ul .icons {
  animation: none;
}

nav .mainMenu li a {
  font-weight: bold;
  color: var(--dark-primary-color);
  padding: 21px;
  display: inline-block;
  text-decoration: none;
  font-size: 2rem;
}

.anchor {
  display: block;
  height: 70px;
  margin-top: -70px;
  visibility: hidden;
}

.fixed {
  position: fixed;
  top: 0;
  z-index: 1;
}

@media (max-width: 850px) {
  .header {
    font-size: 3.5rem;
  }

  #threejs-btn {
    width: 100% !important;
  }
}

nav .mainMenu .closeMenu,
.icons i {
  font-size: 2rem;
  display: none;
  cursor: pointer;
}

.fa-linkedin:hover {
  color: rgb(0, 0, 204) !important;
}

.fa-twitter:hover {
  color: rgb(86, 154, 243) !important;
}

.fa-instagram:hover {
  color: rgb(255, 0, 191) !important;
}

.fa-github:hover {
  color: rgb(255, 123, 0) !important;
}

.icons {
  display: none;
}

@media (max-width: 867px) {
  nav .mainMenu {
    min-height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--purple-primary-color);
    transition: top 1s ease;
    display: none;
  }

  nav .mainMenu .closeMenu {
    display: block;
    position: absolute;
    top: 20px;
    right: 20px;
    color: #fff;
  }

  nav .openMenu {
    display: block;
    text-align: center;
    color: var(--purple-primary-color);
  }

  nav .mainMenu li a:hover {
    background: none;
    color: var(--yellow-primary-color);
  }

  .icons {
    display: inline-flex;
  }

  .icons i {
    display: inline-block;
    padding: 12px;
    color: #fff;
  }

  nav .mainMenu li a {
    color: #fff;
  }

  ::-webkit-scrollbar {
    width: 1.25vw !important;
  }
}

@keyframes nav-load {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes names-header {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes nav-link-load {
  0% {
    transform: scale(0);
  }

  80% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes nav-odd-link-load {
  0% {
    transform: translateY(10000%);
  }

  90% {
    transform: translateY(-50%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes nav-even-link-load {
  0% {
    transform: translateY(-1000%);
  }

  90% {
    transform: translateY(50%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes header-load {
  0% {
    transform: translateY(100%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes logo-header {
  0% {
    transform: translateY(-1000%);
  }

  100% {
    transform: translateY(0);
  }
}

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ End of Header ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/
/*═════════════════════╣ Start of About Me ╠═════════════════════*/
/*⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜⚜*/

#Matin-Pic {
  background: var(--purple-secondary-color);
  animation: MyPic-load ease-in 300ms var(--MyPic-delay-time);
  animation-fill-mode: forwards;
  opacity: 0;
  transform: translate(0, 0);
  min-height: 71vh;
}

.Matin-Picture {
  user-select: none;
  margin: 15px auto;
  color: #ffffff;
  width: 380px;
  background: var(--purple-primary-color);
  text-align: center;
  border-radius: 5px;
  box-shadow: 0 10px 20px -10px rgba(0, 0, 0, 0.75);
  animation: float-lg-cardProfile 35s ease-in-out infinite;
}

.cover-photo {
  background: var(--blue-light-color);
  height: 130px;
  width: 100%;
  border-radius: 5px 5px 0 0;
}

.non-profile {
  height: 130px;
  width: 100%;
}

.profile {
  width: 100%;
  max-width: 127px;
  height: auto;
  object-fit: cover;
  border-radius: 50%;
  border: 1px solid var(--purple-tertiary-color);
  padding: 7px;
  background: #292343;
  margin: -100px 0 0 203px;
  transition: 0.4s ease-in-out;
}

.profile:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.profile-name {
  font-size: 25px;
  font-weight: bold;
  margin: 25px 0 0 -100px;
}

.about {
  margin-top: 35px;
  line-height: 25px;
  font-size: 1.7rem;
}

#Matin-Pic button {
  margin: 15px 10px 30px;
}

.msg-btn,
.follow-btn {
