footer {
  min-height: 25vh;
  width: 100%;
  text-align: center;
}

.footer-icons {
  list-style: none;
  margin: 0 20px;
  padding: 1.5rem 0;
}

.footer-icons li {
  width: 70px;
  height: 70px;
  position: relative;
  display: inline-block;
  margin: 10px;
  cursor: pointer;
}

.footer-icons li::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(45deg, var(--purple-primary-color), #6658a7);
  z-index: -1;
  border-radius: 10%;
  transition: all 0.3s ease-in;
}

.footer-icons li:hover::before {
  transform: rotate(360deg);
  border-radius: 100%;
}

.footer-icons li a span {
  color: #fff;
  line-height: 70px;
  font-size: 27px;
  transition: all 0.3s ease-out;
}

.footer-icons li:hover a span {
  transform: scale(1.2);
  color: #fff !important;
}

.copyright-matin {
  border-top: 2px dashed #000;
  padding: 4rem 0;
  font-size: 1.4rem;
  margin: 0 15px;
}

.copyright-matin a {
  text-decoration: none;
  color: #000;
  transition: 0.3s ease-out;
  letter-spacing: 1px;
}

.copyright-matin a:hover {
  color: #b3b300;
}

@media (max-width: 170px) {
  .copyright-matin a {
    font-size: 1.1rem;
  }
}

@media (max-width: 140px) {
  .copyright-matin a {
    font-size: 0.9rem;
  }
}

@media (min-width: 1900px) {
  html {
    font-size: 75%;
  }

  #AboutMe-image img:nth-child(1) {
    /* animation: float-wideScreen 6s ease-in-out infinite; */
    margin-top: 10rem !important;
  }

  #GoToTopButton {
    bottom: 50px;
    right: 60px;
  }

  .section-1 h1 {
    font-size: 4rem;
  }

  .section-1 p:nth-child(2) {
    font-size: 2rem;
    font-weight: bold;
  }

  .section-1 p:nth-child(3) {
    font-size: 1.8rem;
    line-height: 3.7rem;
  }

  #AboutMe-image img:nth-child(1) {
    margin-top: 25rem;
    animation: float-xlg 6s ease-in-out infinite;
  }

  #Comment {
    min-height: 95vh;
  }

  h2 {
    margin-top: 6rem !important;
  }

  #entrepreneur-quotes-container {
    flex-direction: column;
  }
}

